import React from "react";
import { Navigate } from "react-router-dom";
import Index from "../views/index";
import Footer from "../layout/Footer";
import Page404 from "../views/page404";
import Welcome from "../views/welcome";
import routeList from "./modules";
export const routes = [
  {
    path: "/index",
    element: <Navigate to="/index/welcome" />,
  },
  {
    path: "/index",
    element: <Index />,
    children: [
      {
        path: "welcome",
        element: <Welcome />,
      },
      {
        path: "contents",
        element: <Navigate to="/index/contents/javascript" />,
      },
      {
        path: "contents",
        element: <Footer />,
        children: routeList,
      },
    ],
  },
  {
    path: "/",
    element: <Navigate to="/index/welcome" />,
  },
  {
    path: "*",
    element: <Page404 />,
  },
];
