import { Anchor } from "antd";
import React, { useEffect, useState } from "react";
import style from "./index.module.scss";
import { NavSiderList } from "../../api";
import { useLocation, useNavigate } from "react-router";
const { Link } = Anchor;

// 订阅 消息
const toChinesNum = (num) => {
  let changeNum = ["零", "一", "二", "三", "四", "五", "六", "七", "八", "九"]; //changeNum[0] = "零"
  let unit = ["", "十", "百", "千", "万"];
  num = parseInt(num);
  let getWan = (temp) => {
    let strArr = temp.toString().split("").reverse();
    let newNum = "";
    for (let i = 0; i < strArr.length; i++) {
      newNum =
        (i === 0 && strArr[i] === 0
          ? ""
          : i > 0 && strArr[i] === 0 && strArr[i - 1] === 0
          ? ""
          : changeNum[strArr[i]] + (strArr[i] === 0 ? unit[0] : unit[i])) + newNum;
    }
    return newNum;
  };
  let overWan = Math.floor(num / 10000);
  let noWan = num % 10000;
  if (noWan.toString().length < 4) noWan = "0" + noWan;
  return overWan ? getWan(overWan) + "万" + getWan(noWan) : getWan(num);
};
export default function SiderLeft() {
  let [siderKey, setSiderkey] = useState("JavaScriptBasic");
  let [siderList, setSiderList] = useState([]); // 侧边栏列表
  let location = useLocation();
  let navigate = useNavigate();
  // antd Anchor底层是使用的a标签实现锚点功能的，而a标签的默认行为就是路由跳转（跳转到href），阻止a标签的默认行为 就不会跳转了
  const handleAnchorClick = (e, link) => {
    e.preventDefault();
    // 记录锚点 用于返回上一级
    navigate(location.pathname, {
      state: { pathKey: siderList[0].pKey, href: link.href.split("#")[1] },
    });
  };
  // 跳转到指定锚点
  const toLinks = (href) => {
    if (href) {
      let ele = document.getElementById(href);
      ele && ele.scrollIntoView({ block: "start", behavior: "smooth", inline: "start" });
    }
  };

  useEffect(() => {
    // 跳转到搜索对应锚点
    if (location.state) {
      let data = location.state;
      if (data.pathKey && data.href) {
        setSiderkey(data.pathKey);
        //  检查dom是否执行完成
        let timer = null;
        function check() {
          let dom = document.getElementById(data.href);
          if (dom) {
            //  执行dom加载完成后的操作
            toLinks(data.href);
            if (!timer) {
              clearTimeout(timer);
            }
          } else {
            //  自我调用
            timer = setTimeout(check, 0);
          }
        }
        // 调用
        check();
      } else if (data.pathKey) {
        setSiderkey(data.pathKey);
      }
    }
    return () => {};
  }, [location]);
  // 改变路由刷新侧边栏
  useEffect(() => {
    NavSiderList({ key: siderKey }).then((res) => {
      if (res.status === 200) {
        console.log(res.data);
        setSiderList(res.data.list);
      }
    });
  }, [location.pathname]);
  return (
    <div className={style.left}>
      <div className="list">
        <Anchor
          showInkInFixed={{ affix: false }}
          onClick={handleAnchorClick}
          style={{ paddingLeft: "10px", paddingBottom: "100px" }}
          getContainer={() => document.getElementById("content")}
        >
          {siderList.map((item, index) => {
            return (
              <Link key={item.key} href={"#" + item.key} title={toChinesNum(index + 1) + "、" + item.title}>
                {item.children?.map((val, idx) => {
                  return <Link key={val.key} href={"#" + val.key} title={idx + 1 + "、" + val.title}></Link>;
                })}
              </Link>
            );
          })}
        </Anchor>
      </div>
    </div>
  );
}
