import { Menu, Select, Spin } from "antd";
import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { FolderOpenOutlined } from "@ant-design/icons";
import style from "./index.module.scss";
import { NavBarList, NavSearchList } from "../../api";
import _ from "lodash";
const { Option, OptGroup } = Select;
const HeaderTop = () => {
  const [items, setItems] = useState([]); // 菜单
  const [current, setCurrent] = useState(null); // 当前激活项
  const navigate = useNavigate(); // 编程式路由
  const location = useLocation(); // 获取路由名称, 监听路由
  let [searchList, setSearchList] = useState([]); // 搜索下拉菜单
  // 前往首页
  const toIndex = () => {
    navigate("/");
  };
  // 确定搜索
  const onSelectMenu = (e, item) => {
    console.log(item);
    if (item) {
      navigate(item.path, {
        replace: false,
        state: { pathKey: item.pKey, href: item.key },
      });
    }
  };
  useEffect(() => {
    // 获取搜索数据
    NavSearchList().then((res) => {
      if (res.status === 200) {
        setSearchList(res.data.list);
        // setLoading(false);
      }
    });

    return () => {};
  }, []);
  useEffect(() => {
    (async () => {
      let { data } = await NavBarList();
      let list = []; // 获取头部导航栏内容
      for (const t of data.list) {
        list.push({
          key: t.key,
          label: t.title,
          icon: <FolderOpenOutlined />,
          children: t.children?.map((val) => {
            // 如果是通过url进入 高亮显示当前激活项
            if (val.path === location.pathname) {
              setCurrent(val.key);
            }
            return {
              key: val.key,
              label: val.title,
              path: val.path,
              disabled: val.disabled,
            };
          }),
        });
      }
      setItems(list);
    })();
    return () => {
      // 返回主页重置状态
      setCurrent(null);
    };
  }, [location.pathname]);

  // 切换菜单
  const onClickMenu = (e) => {
    items.find((val) => {
      val.children?.find((v) => {
        if (v.key === e.key) {
          // 路由跳转
          navigate(v.path, {
            replace: false,
            state: { pathKey: e.key },
          });
          setCurrent(e.key);
        }
      });
    });
  };
  return (
    <div className={style.header_top}>
      {" "}
      <div className={style.nav}>
        <div className={style.user} onClick={toIndex}>
          <img
            className={style.logo}
            src={
              "https://thirdwx.qlogo.cn/mmopen/vi_32/IQ5Ux3dw2YiagIt4j9N7HmE0EZmRiaYpibRl2aHicexicqQyC63CxO20qUTCymhlOkLaCJp9O59hELVuePaL1zbicRlw/132"
            }
            alt=""
          />{" "}
          <span>water</span>{" "}
        </div>
        <Menu onClick={onClickMenu} selectedKeys={[current]} mode="horizontal" items={items} />
        <Select
          showSearch
          style={{
            width: 300,
          }}
          size="large"
          placeholder="Search to Select"
          onSelect={onSelectMenu}
        >
          {searchList.map((val) => {
            return (
              <OptGroup label={val.title} key={val.key}>
                {val.children?.map((v) => {
                  return v.children.length !== 0 ? (
                    v.children?.map((el) => {
                      return (
                        <Option value={v.title + "=>" + el.title} key={el.key} path={val.path} pKey={val.key}>
                          {v.title + "=>" + el.title}
                        </Option>
                      );
                    })
                  ) : (
                    <Option value={v.title + "=>" + v.title} key={v.key} path={val.path} pKey={val.key}>
                      {v.title + "=>" + v.title}
                    </Option>
                  );
                })}
              </OptGroup>
            );
          })}
        </Select>
      </div>
    </div>
  );
};
export default HeaderTop;
