import React from "react";
import { useState, useEffect, useRef } from "react";
import typewriter from "react-typewriter-hook";
import axios from "axios";

export default function AutoTypewriter() {
  const [magicName, setMagicName] = useState(`Loading...`);
  const intervalRef = useRef({});
  const name = typewriter(magicName);
  useEffect(() => {
    intervalRef.current = setInterval(() => {
      axios({
        url: "https://v1.hitokoto.cn/?encode=text&c=j",
      }).then((res) => {
        setMagicName(res.data);
      });
    }, 5000);
    return function clear() {
      console.log("clear");
      clearInterval(intervalRef.current);
    };
  }, [magicName]);
  return (
    <div
      style={{
        position: "absolute",
        left: "50%",
        top: "30%",
        transform: "translate(-50%,-50%)",
        width: "100%",
        fontSize: "40px",
        textAlign: "center",
        background: "-webkit-linear-gradient(315deg,#42d392 25%,#647eff)",
        WebkitBackgroundClip: "text",
        WebkitTextFillColor: "transparent",
        userSelect: "none",
        fontFamily: "star",
      }}
    >
      「 {name} 」
    </div>
  );
}
